.input__container{display:flex;align-items:stretch;width:100%;border-radius:3px;background:#fff}.input__container_size_l{height:60px}.input__container_size_l .form__input{padding-inline:20px}.input__container_size_s{height:43px}.input__container_size_s .form__input{padding-inline:8px;font-size:1.125rem}.form__input{width:100%;padding-block:0;border:none;outline:none;font-size:1.3rem;background:rgba(0,0,0,0);position:relative}.form__input[type=date][value=""]{text-transform:uppercase}.form__input[type=date][value=""]:not(:focus)::before{content:"ДД/ММ/ГГГГ";position:absolute;top:0;bottom:0;width:100%;display:flex;align-items:center;background:#fff;color:#e5e5e5;pointer-events:none}.form__input::placeholder{color:#e5e5e5}input[type=date]::-webkit-inner-spin-button,input[type=date]::-webkit-calendar-picker-indicator{display:none}.input__btn{display:block;height:100%;padding:0;padding-right:20px}.input__btn_icon{display:block;height:50%;fill:#e5e5e5}.date-input__wrapper{width:100%;position:relative;display:flex;flex-direction:column;align-items:center}