height:240px;margin-bottom:15px}}.feature__description{font-size:24px;line-height:28px;text-align:center}.reviews{max-width:1520px;margin:0 auto;padding-inline:50px}.reviews__list{margin-top:70px;margin-bottom:135px;display:flex;gap:85px}@media(max-width: 1200px){.reviews__list{flex-wrap:wrap;justify-content:center;gap:40px}}.reviews__item{display:flex;align-items:center;width:50%;text-align:left}@media(max-width: 1200px){.reviews__item{width:100%}}.reviews{padding-block:8% 6.25%}.review__image{width:205px;height:205px;border-radius:50%}.review__image_wrapper{flex-shrink:0;margin-right:35px}.review__header{color:#000}.review__text{position:relative;margin-top:30px;font-size:18px;font-weight:300;font-style:italic;color:#918f94;line-height:21px}.review__text::before,.review__text::after{position:absolute;font-weight:500;font-size:36px;color:#928f94}.review__text::before{content:"“";top:0;left:-20px}.review__text::after{content:"”";bottom:-20px}.carousel .control-dots .dot.dot{width:19px;height:19px;margin-inline:5px;background:#e5e5e5;opacity:1;box-shadow:none}.carousel .control-dots .dot.dot.selected{background:#c4c4c4}